.contacts {
  position: relative;
  padding-top: 30px;
  padding-bottom: 30px;
}

.contacts__wrapper {
  box-sizing: border-box;
  min-width: 320px;
  max-width: 1150px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.contacts__text {
  font-weight: bold;
  font-size: 18px;
  line-height: 30px;
  padding-top: 5px;
  position: relative;
}

.contacts__list {
  margin: 25px auto 0;
  max-width: 350px;
  font-size: 15px;
  line-height: 30px;
  color: #444444;
}

.contacts__list-item {
  list-style: none;
}

.contacts__value,
.contacts__field {
  display: inline-block;
  vertical-align: top;
  margin-bottom: 30px;
}

.contacts__value {
  width: 50px;
}

.contacts__field {
  width: 156px;
  margin-left: 21px;
}

.contacts__link {
  color: #000000;
  border-bottom: 1px solid #63d1bb;
}

.contacts__link:hover {
  color: #46c1ae;
}

.contacts__link:active {
  color: rgba(70, 193, 174, 0.3);
  border-bottom: 1px solid rgba(70, 193, 174, 0.3);
}

.contacts__map {
  position: relative;
  width: 322px;
  height: 457px;
  margin-top: 8px;
}

.contacts__map-image {
  width: 322px;
  height: 457px;
}

.contacts__map-image-marker {
  z-index: 100;
  background-image: url("../img/map-pin-svg.png");
  background-repeat: no-repeat;
  width: 67px;
  height: 100px;
  top: 124px;
  left: 157px;
  position: absolute;
}

.contacts__button {
  box-sizing: border-box;
  margin: 0 auto;
  min-width: 320px;
  max-width: 1150px;
  padding: 30px;
}

@media (min-width: 768px) {
  .contacts {
    padding-top: 67px;
    padding-bottom: 66px;

    &::before {
      background-size: auto;
    }
  }

  .contacts__map {
    width: 768px;
    height: 457px;
  }

  .contacts__wrapper {
    padding-left: 50px;
    padding-right: 50px;
  }

  .contacts__text {
    padding-bottom: 20px;
    font-weight: bold;
    font-size: 43px;
    line-height: 50px;

    &::before {
      content: "";
      position: absolute;
      right: 3px;
      top: -10px;
      width: 47px;
      height: 81px;
      background-image: url("../img/icon-flag.svg");
      background-repeat: no-repeat;
    }
  }

  .contacts__list {
    display: flex;
    flex-wrap: wrap;
    margin-top: 29px;
    margin-bottom: 26px;
    max-width: 668px;
    font-size: 17px;
    line-height: 30px;
    color: #444444;
  }

  .contacts__value {
    width: 55px;
  }

  .contacts__field {
    margin-left: 19px;
    width: 254px;
  }

  .contacts__map-image {
    width: 768px;
    height: 457px;
  }

  .contacts__map-image-marker {
    top: 138px;
    left: 363px;
  }

  .contacts__button {
    padding-top: 49px;
    padding-left: 50px;
    padding-right: 50px;
    padding-bottom: 40px;
  }

  .contacts__button-item {
    width: 230px;
  }
}

@media (min-width: 1150px) {
  .contacts {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    padding: 89px 50px 142px 50px;
    width: 1050px;
  }

  .contacts__wrapper {
    margin: 0;
    width: 45%;
    padding-top: 73px;
    padding-bottom: 10px;
    padding-left: 168px;
    padding-right: 0;
  }

  .contacts__text {
    padding-bottom: 20px;
    font-weight: bold;
    font-size: 53px;
    line-height: 57px;
    color: #000000;

    &::before {
      left: -100px;
    }
  }

  .contacts__list {
    max-width: 720px;
    display: flex;
    flex-wrap: wrap;
  }

  .contacts__list-item {
    display: flex;
    flex-wrap: wrap;
  }

  .contacts__value {
    margin-bottom: 30px;
  }

  .contacts__field {
    margin-bottom: 30px;
    margin-left: 29px;
    width: 220px;
  }

  .contacts__map {
    position: absolute;
    width: 525px;
    height: 457px;
    right: 50px;
  }

  .contacts__map-image {
    width: 525px;
  }

  .contacts__map-image-marker {
    top: 133px;
    left: 246px;
  }

  .contacts__button-item {
    width: 230px;
    margin-top: -107px;
    margin-left: -399px;
  }
}
